import {
  SimpleGrid,
  Heading,
  Image,
  Box,
  Button,
  color,
  Input,
  Text,
} from "@chakra-ui/react";
import React, { useEffect } from "react";
import { AiFillLinkedin, AiFillGithub, AiOutlineMail } from "react-icons/ai";
import { FiPhoneCall } from "react-icons/fi";
import { useState } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import emailimg from "../Image/emailimg.png";

const Contact = (props) => {
  useEffect(() => {
    AOS.init({ offset: 300, duration: 1000 });
  }, []);

  const { checkTheme } = props;
  const [theme, setTheme] = useState({});
  const [theme1, setTheme1] = useState({});
  useEffect(() => {
    if (checkTheme) {
      setTheme({
        color: "white",
        backgroundColor: "#0f1624",
      });
    } else {
      setTheme({
        color: "black",
        backgroundColor: "#edf2f8",
      });
    }
  }, [checkTheme]);

  useEffect(() => {
    if (checkTheme) {
      setTheme1({
        color: "white",
        backgroundColor: "#1a202c",
      });
    } else {
      setTheme1({
        color: "black",
        backgroundColor: "#ffffff",
      });
    }
  }, [checkTheme]);

  return (
    <div className="mainDiv" style={theme}>
      <Heading fontSize={40} color="#fe9119">
        Contact <span style={theme}>Me</span>
      </Heading>
      <SimpleGrid columns={[1,1,2,2]} pb="70px">
        <Image 
        mr={["auto","auto","auto","-100px"]}
        data-aos="zoom-in" mt={30} m="auto" h={["300px","300px","400px","450px"]} src={emailimg} alt="emailimg" />
        <SimpleGrid data-aos="zoom-in">
          <Box display={"flex"} m="auto" gap={[2,2,3,8]} mt="100px">
            <a href="http://www.linkedin.com/in/abhishekpratapsolanki">
            <Button
              border="2px solid #0a66c2"
              fontSize={[14,14,14,17]}
              color="#ffff"
              bg="#0a66c2"
              p={"0"}
              pl={[1,1,0,3]}
              pr={[2,2,1,4]}
              transition="all .4s ease-in-out"
              _hover={{
                transform: "scale(1.1)",
                bg: "#ffff",
                color: "#0a66c2",
              }}
              data-aos="zoom-in"
            >
              <AiFillLinkedin style={{ marginRight: "4", fontSize: "20px" }} />
              Linkdin
            </Button>
            </a>
            <a href="https://github.com/Abhishek07788">
            <Button
              border="2px solid #3f3d55"
              fontSize={[14,14,14,17]}
              color="#ffff"
              bg="#3f3d55"
              p={"0"}
              pl={[1,1,0,3]}
              pr={[2,2,1,4]}
              transition="all .4s ease-in-out"
              _hover={{
                transform: "scale(1.1)",
                bg: "#ffff",
                color: "#3f3d55",
              }}
               data-aos="zoom-in"
            >
              <AiFillGithub style={{ marginRight: "4", fontSize: "20px" }} />
              Github
            </Button>
            </a>
            <a href="mailto:abhisheksolanki1020@gmail.com">
            <Button
              border="2px solid #f04244"
              fontSize={[14,14,14,17]}
              color="#ffff"
              bg="#f04244"
              p={"0"}
              pl={[1,1,0,3]}
              pr={[2,2,1,4]}
              transition="all .4s ease-in-out"
              _hover={{
                transform: "scale(1.1)",
                bg: "#ffff",
                color: "#f04244",
              }}
              data-aos="zoom-in"
            >
              <AiOutlineMail style={{ marginRight: "4", fontSize: "20px" }} />
              Email
            </Button>
            </a>
            <a href="tel:+919758207006">
            <Button
              border="2px solid #4db946"
              fontSize={[14,14,14,17]}
              color="#ffff"
              bg="#4db946"
              p={"0"}
              pl={[1,1,0,3]}
              pr={[1,1,0,3]}
              transition="all .4s ease-in-out"
              _hover={{
                transform: "scale(1.1)",
                bg: "#ffff",
                color: "#4db946",
              }}
              data-aos="zoom-in"
            >
              <FiPhoneCall style={{ marginRight: "4", fontSize: "20px" }} />
            </Button>
            </a>
          </Box>
          <Box m={"auto"} mt="15px" width="65%">
          <Heading  fontSize={26} style={theme} textAlign="left">
        Send me <span style={{color:"#fe9119"}}>Email.</span>
      </Heading>
          <Input style={theme1} placeholder='Full Name' borderBottom={"1px solid grey"} size='md' />
          <Input style={theme1} mt="16px" placeholder='Email' borderBottom={"1px solid grey"} size='md' />
          <Input style={theme1} mt="16px" placeholder='Number' borderBottom={"1px solid grey"} size='md' />
          <textarea style={theme1} id="Message" placeholder="Your Message" className="form-control" name="message" rows="5" required></textarea>
          <Button
              border="2px solid #fe9119"
              fontSize={18}
              mt={1}
              color="black"
              bg="#fe9119"
              w="100%"
              transition="all .4s ease-in-out"
              _hover={{
                bg: "#ffff",
                color: "#fe9119",
              }}
              //data-aos="zoom-in"
            >
              <AiOutlineMail style={{ marginRight: "4", fontSize: "20px" }} />
              Submit
            </Button>
          </Box>
        </SimpleGrid>
      </SimpleGrid>
      <Text pb={2} fontSize={14} w={"90%"} m="auto" textAlign={"center"}>Designed and build by Abhishek Pratap Solanki, 2022 All right reserved.</Text>
      <Box position={"fixed"} bottom="0" ml={[3]} display="grid" gap={2} pb="7">
      <a href="http://www.linkedin.com/in/abhishekpratapsolanki">
            <Button
              border="2px solid #0a66c2"
              color="#ffff"
              bg="#0a66c2"
              fontSize={["16px","17px","18px","20px"]}
              p={0}
              borderRadius="50%"
              transition="all .4s ease-in-out"
              _hover={{
                transform: "scale(1.1)",
                bg: "#ffff",
                color: "#0a66c2",
              }}
            >
              <AiFillLinkedin />
            </Button>
            </a>
            <a href="https://github.com/Abhishek07788">
            <Button
              border="2px solid #3f3d55"
              color="#ffff"
              bg="#3f3d55"
              fontSize={["16px","17px","18px","20px"]}
              p={0}
              borderRadius="50%"
              transition="all .4s ease-in-out"
              _hover={{
                transform: "scale(1.1)",
                bg: "#ffff",
                color: "#3f3d55",
              }}
            >
              <AiFillGithub  />
            </Button>
            </a>
            <a href="mailto:abhisheksolanki1020@gmail.com">
            <Button
              border="2px solid #f04244"
              color="#ffff"
              bg="#f04244"
              fontSize={["16px","17px","18px","20px"]}
              p={0}
              borderRadius="50%"
              transition="all .4s ease-in-out"
              _hover={{
                transform: "scale(1.1)",
                bg: "#ffff",
                color: "#f04244",
              }}
            >
              <AiOutlineMail  />
            </Button>
            </a>
      </Box>
      
    </div>
  );
};

export default Contact;
